
import React, { useEffect, useState } from "react";
import Button from 'react-bootstrap/Button';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Modal } from 'react-bootstrap';
export default class  MultiSharedModal extends React.Component{
  constructor(props){
    super(props)
    this.state={
      showModal:false,
      currentColorBtn:"#122b79"
    }
    this.handleClose=this.handleClose.bind(this)
    this.handleShow=this.handleShow.bind(this)
    this.SwitchModal=this.SwitchModal.bind(this)
    this.OnMouseOver=this.OnMouseOver.bind(this)
    this.OnMouseLeave=this.OnMouseLeave.bind(this)
  }
  
  handleClose(){
    this.setState({showModal:false});
  }
  handleShow(){
    this.setState({showModal:true})
  }
  SwitchModal(){
    this.props.goToNextModal()
    this.handleClose()
  }
  OnMouseOver() {
    this.setState({currentColorBtn:"rgb(28 61 164)"})
  }
  OnMouseLeave() {
    this.setState({currentColorBtn:"#122b79"})
  }

  render(){
    const {showModal}=this.state

    return(
      
      <Modal show={showModal} onHide={this.handleClose} >
        <div style={{textAlign:"center",borderBottom:"solid #122b79",height:"53px"}}>
          
          <h5 style={{position:"relative",top:"30%"}}>הזן את שם התנועה</h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close" style={{position:"relative",top:"-35px",right:"2px"}} onClick={this.handleClose}>
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div style={{height:"100%"}} >
        <input type="text" class="form-control" placeholder="שם תנועה" style={{position:"relative",left:"30%",width:"40%",top:"7%",textAlign:"center"}}/>
      
        <button type="button" class="btn btn-primary" style={{background:"#122b79",borderColor:"#122b79",position:"relative",top:"15px",left:"42%"}} onClick={this.SwitchModal}>המשך</button>
        
        

      </div>
        {/* <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button variant="primary" onClick={handleClose}>
            Save Changes
          </Button>
        </Modal.Footer> */}
      </Modal>
      
    );
  }
  
}