
import React, { useEffect, useState } from "react";
import Button from 'react-bootstrap/Button';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Modal } from 'react-bootstrap';
import ShareModal from './shareModal'
export default class  EmailModal extends React.Component{
  constructor(props){
    super(props)
    this.state={
      showModal:false,
      emailAddress:'',
      nOfFiles:'1'
    }
    this.handleClose=this.handleClose.bind(this)
    this.handleShow=this.handleShow.bind(this)
    this.SwitchModal=this.SwitchModal.bind(this)
    this.sendEmail=this.sendEmail.bind(this)
    this.handleChange=this.handleChange.bind(this)
    this.validateEmail=this.validateEmail.bind(this)
  }
  handleChange(e){
    this.setState({emailAddress:e.target.value})
  }
  handleClose(){
    this.setState({showModal:false});
  }
  handleShow(nOfFiles){
    this.setState({showModal:true,nOfFiles:nOfFiles})
  }
  updateCurrentP(){
    var requestOptions = {
      method: 'POST',
      redirect: 'follow'
    };
    
    fetch("http://10.0.0.5:3001/setcurrentp", requestOptions)
      .then(response => response.text())
      .then(result => console.log(result))
      .catch(error => console.log('error', error));
  }
  sendEmail(){
    this.validateEmail(this.state.emailAddress,(result)=>{
      if(result){
        (async () => {
          const rawResponse = await fetch('http://10.0.0.5:3001/emaillSending', {
            method: 'POST',
            mode:"no-cors",
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/x-www-form-urlencoded'
            },
            body: JSON.stringify({email:this.state.emailAddress,nOfFiles:this.state.nOfFiles})
          });
          const content = await rawResponse.json();
        
          console.log(content);
  
        })();
        this.updateCurrentP()
      }
      else{
        this.setState({emailAddress:""})
      }
    })
   


  }
  SwitchModal(){
    this.sendEmail()
    if(this.state.emailAddress!=''){
      this.props.goToThanksModal()
      this.handleClose()
    }
   
  }
  validateEmail(emailAddress,fn){
    var isValid=true
    if(!/^[a-zA-Z0-9]+@(?:[a-zA-Z0-9]+\.)+[A-Za-z]+$/.test(this.state.emailAddress)){
      alert("Wrong Email Format")
      isValid=false
    }
   fn(isValid)
  }
  

  render(){
    const showModal=this.state.showModal
    const textInput=this.state.emailAddress
    return(
      <div style={{height:"100%"}}>
            <Modal show={showModal} onHide={this.handleClose} >
        <div style={{textAlign:"center",borderBottom:"solid #122b79",height:"53px"}}>
          
          <h5 style={{position:"relative",top:"30%"}}>הזן דואר אלקטרוני </h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close" style={{position:"relative",top:"-35px",right:"2px"}} onClick={this.handleClose}>
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div style={{height:"100%"}} >
        <input type="text" class="form-control" placeholder="מייל" style={{position:"relative",left:"30%",width:"40%",top:"7%",textAlign:"center"}} onChange={this.handleChange} value={textInput}/>
      
        <button type="button" class="btn btn-primary" style={{background:"#122b79",borderColor:"#122b79",position:"relative",top:"15px",left:"42%"}} onClick={this.SwitchModal}>שתף</button>
        
        

      </div>
        {/* <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button variant="primary" onClick={handleClose}>
            Save Changes
          </Button>
        </Modal.Footer> */}
      </Modal>
      <ShareModal goToEmailModal={this.handleShow}></ShareModal>
      </div>

      
      
    );
  }
  
}