
import React, { useEffect, useState } from "react";
import Button from 'react-bootstrap/Button';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Modal } from 'react-bootstrap';
import ThankUModal from  "./thankUModal"
export default class  MultiSharedModal extends React.Component{
  constructor(props){
    super(props)
    this.state={
      showModal:false,
      currentColorBtn:"#122b79",
      phoneNumber:''
    }
    this.handleClose=this.handleClose.bind(this)
    this.handleShow=this.handleShow.bind(this)
    this.handleChange=this.handleChange.bind(this)
    this.phoneValidation=this.phoneValidation.bind(this)
    this.sendNotification=this.sendNotification.bind(this)
    
    
  }
  
  handleClose(){
    this.setState({showModal:false});
  }
  handleShow(){
    this.setState({showModal:true})
  }
  
  handleChange(e){
    this.setState({phoneNumber:e.target.value})
  }
  phoneValidation(phoneNumber,fn){
    var isValid=true
    if(!(/^\+?(972|0|\+972|00972)(\-)?(([23489]{1}\d{7})|[5]{1}\d{8})/.test(phoneNumber))){
      alert("Wrong phone number")
      isValid=false
    }
    fn(isValid)
  }
  sendNotification(){
      this.phoneValidation(this.state.phoneNumber,(result)=>{
      if(result){console.log("phoneNumber")}
      else{this.setState({phoneNumber:''})}
    })
    
  }
  render(){
    const {showModal}=this.state
    const textInput=this.state.phoneNumber
    return(
      <div style={{height:"15%"}}>
          <Modal show={showModal} onHide={this.handleClose} >
        <div style={{textAlign:"center",borderBottom:"solid #122b79",height:"53px"}}>
          
          <h5 style={{position:"relative",top:"30%"}}>הזן מספר טלפון לתזכורת</h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close" style={{position:"relative",top:"-35px",right:"2px"}} onClick={this.handleClose}>
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div style={{height:"100%"}} >
          <input type="text" class="form-control" placeholder="מספר טלפון" style={{position:"relative",left:"30%",width:"40%",top:"7%",textAlign:"center"}} onChange={this.handleChange} value={textInput}/> 
          <button type="button" class="btn btn-primary" style={{background:"#122b79",borderColor:"#122b79",position:"relative",top:"15px",left:"42%"}} onClick={this.sendNotification}>טלפון</button>
      </div>
      </Modal>
      <ThankUModal showPNumberModal={this.handleShow}/>      
      </div>
      
      
    );
  }
  
}